@import url("https://fonts.cdnfonts.com/css/roboto");
@import url("https://fonts.cdnfonts.com/css/inter");
@import url("https://fonts.cdnfonts.com/css/roboto");
@import url("https://fonts.cdnfonts.com/css/inter");
* {
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-size: 12px;
  font-weight: 400;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  width: auto;
  text-decoration: none;
  list-style-type: none;
  max-width: 100%;
}
*:focus {
  outline: none;
}

.input {
  display: flex;
}
.input input {
  width: 263px;
  height: 50px;
  display: block;
  padding: 9px 45px 9px 20px;
  border: 0;
  border-radius: 50px;
  background-color: #E5E5E5;
}
.input input::placeholder {
  color: #bbb;
}
.input button {
  position: relative;
  margin-top: -44px;
  margin-left: 217px;
  min-width: 10px;
}
.input button img {
  min-width: 100%;
}

.btn {
  font-weight: 700;
  font-size: 14px;
  white-space: nowrap;
  color: #FFFFFF;
  background-color: #FFC85E;
  border: 0;
  border-radius: 50px;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.btn img {
  background-color: #FFC85E;
}
.btn__text {
  display: flex;
  justify-content: space-between;
  padding: 20px 40px;
}
.btn__text img {
  margin-left: 20px;
  min-width: 100%;
}
.btn__img {
  height: 39px;
  width: 39px;
  display: flex;
  align-items: center;
}
.btn__img img {
  height: 14px;
  width: 14px;
}
.btn__cart {
  cursor: pointer;
  display: flex;
  border: 0;
  background-color: #FFFFFF;
}
.btn__cart .btn__indicator {
  color: #FFFFFF;
  position: relative;
  margin-top: -8px;
  margin-left: -15px;
  padding: 12px;
  width: 10px;
  height: 10px;
}

.footer {
  overflow: hidden;
  padding: 70px 0;
  background-color: #3F4E65;
  color: #FFFFFF;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.logo {
  width: 160px;
}

.promo__img {
  width: 70px;
  margin: 5px;
}

.long {
  max-width: 300px;
}

.col {
  max-width: 10vw;
  display: flex;
  margin: 0 20px;
  flex-direction: column;
}
.col h3 {
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 150%;
}

.row {
  display: flex;
  flex-direction: row;
}

.link {
  color: #FFFFFF;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
}

@media (max-width: 730px) {
  .footer {
    padding: 10px 0;
    display: flex;
    flex-direction: column;
  }
  .col {
    max-width: 500px;
  }
}

