
//-----------------imports------------------

@import '../styles/vars'
@import '../styles/fonts'
@import '../styles/styles'

//-----------------commons------------------


li a, strong p
  color: $gray

strong
  font-style: normal
  font-weight: 600
  font-size: 14px
  line-height: 130.02%
.contact__container
  display: flex
  flex-direction: row
  align-items: center
  padding: 10px 30px
  p
    margin: 0 10px

.header
  background-color: $white

.menu__open
  display: none
.head
  display: flex
  flex-direction: row
  justify-content: space-between
  align-items: center
  padding: 0 10vw


.contact
  display: flex
  flex-direction: row

.navigation
  display: flex
  flex-direction: row
  &__item
    margin: 10px
    padding: 10px

.nav
  padding: 24px 10vw
  border-top: 1px solid rgba($gray, 0.2)
  border-bottom: 1px solid rgba($gray, 0.2)
  display: flex
  flex-direction: row
  align-items: center
  justify-content: space-between
  &__img
    position: relative
    height: 60px
    width: 70px
    img
      position: absolute
      top: -22px


.col
  display: flex
  flex-direction: column

.mobile
  display: none

@media (max-width: 730px)
  .nav
    padding: 0 10px

  .head
    display: none
  .col
    display: none

  .menu__open
    display: flex
    background-color: $yellow
    align-items: center
    border: 0
    border-radius: 50px
    padding: 15px
    img
      width: 20px
  .nav
    display: flex

  .mobile
    color: $gray
    display: flex
    align-items: flex-start
    justify-content: space-around
    &__img g
      fill: $white
    &__img path
      stroke: $gray
      stroke-width: 1px

    &__btn
      color: $gray
      display: flex
      align-items: center
      img
        width: 30px

