@import url("https://fonts.cdnfonts.com/css/roboto");
@import url("https://fonts.cdnfonts.com/css/inter");
@import url("https://fonts.cdnfonts.com/css/roboto");
@import url("https://fonts.cdnfonts.com/css/inter");
* {
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-size: 12px;
  font-weight: 400;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  width: auto;
  text-decoration: none;
  list-style-type: none;
  max-width: 100%;
}
*:focus {
  outline: none;
}

.input {
  display: flex;
}
.input input {
  width: 263px;
  height: 50px;
  display: block;
  padding: 9px 45px 9px 20px;
  border: 0;
  border-radius: 50px;
  background-color: #E5E5E5;
}
.input input::placeholder {
  color: #bbb;
}
.input button {
  position: relative;
  margin-top: -44px;
  margin-left: 217px;
  min-width: 10px;
}
.input button img {
  min-width: 100%;
}

.btn {
  font-weight: 700;
  font-size: 14px;
  white-space: nowrap;
  color: #FFFFFF;
  background-color: #FFC85E;
  border: 0;
  border-radius: 50px;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.btn img {
  background-color: #FFC85E;
}
.btn__text {
  display: flex;
  justify-content: space-between;
  padding: 20px 40px;
}
.btn__text img {
  margin-left: 20px;
  min-width: 100%;
}
.btn__img {
  height: 39px;
  width: 39px;
  display: flex;
  align-items: center;
}
.btn__img img {
  height: 14px;
  width: 14px;
}
.btn__cart {
  cursor: pointer;
  display: flex;
  border: 0;
  background-color: #FFFFFF;
}
.btn__cart .btn__indicator {
  color: #FFFFFF;
  position: relative;
  margin-top: -8px;
  margin-left: -15px;
  padding: 12px;
  width: 10px;
  height: 10px;
}

li a, strong p {
  color: #3F4E65;
}

strong {
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 130.02%;
}

.contact__container {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 30px;
}
.contact__container p {
  margin: 0 10px;
}

.header {
  background-color: #FFFFFF;
}

.menu__open {
  display: none;
}

.head {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 10vw;
}

.contact {
  display: flex;
  flex-direction: row;
}

.navigation {
  display: flex;
  flex-direction: row;
}
.navigation__item {
  margin: 10px;
  padding: 10px;
}

.nav {
  padding: 24px 10vw;
  border-top: 1px solid rgba(63, 78, 101, 0.2);
  border-bottom: 1px solid rgba(63, 78, 101, 0.2);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.nav__img {
  position: relative;
  height: 60px;
  width: 70px;
}
.nav__img img {
  position: absolute;
  top: -22px;
}

.col {
  display: flex;
  flex-direction: column;
}

.mobile {
  display: none;
}

@media (max-width: 730px) {
  .nav {
    padding: 0 10px;
  }
  .head {
    display: none;
  }
  .col {
    display: none;
  }
  .menu__open {
    display: flex;
    background-color: #FFC85E;
    align-items: center;
    border: 0;
    border-radius: 50px;
    padding: 15px;
  }
  .menu__open img {
    width: 20px;
  }
  .nav {
    display: flex;
  }
  .mobile {
    color: #3F4E65;
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
  }
  .mobile__img g {
    fill: #FFFFFF;
  }
  .mobile__img path {
    stroke: #3F4E65;
    stroke-width: 1px;
  }
  .mobile__btn {
    color: #3F4E65;
    display: flex;
    align-items: center;
  }
  .mobile__btn img {
    width: 30px;
  }
}

